.landing-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgba(1, 13, 22, 0.8), rgba(1, 13, 22, 0.8)),
        url("https://images.unsplash.com/photo-1526406915894-7bcd65f60845?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=924&q=80")
            no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    width: 100%;
    height: 35em;
}

.landing-title {
    text-align: center;
    padding: 15%;
    font-size: 42px;
    text-shadow: 2px 2px black;
    color: white;
}

.list-option-headers {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: white;
    width: 70%;
    margin: 0% 15%;
    height: 30em;
}
.app-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 22px;
    padding: 5px;
    box-shadow: 2px 2px #9ecae1;
    background-color: #deebf7;
    color: black;
    font-family: "Roboto", sans-serif;
}

.app-header-item {
    font-weight: 500;
    font-size: 16px;
    color: black;
    margin: 10px;
    padding: 10px;
    height: 30px;
    width: 33%;
}

.app-header-input {
    font-weight: 500;
    font-size: 16px;
    color: black;
    margin: 10px;
    padding: 10px;
    height: 30px;
    width: 63%;
}

.app-section-header {
    font-family: "Roboto", sans-serif;
    margin: 10px;
}

.sign-out {
    font-family: "Roboto", sans-serif;
}

.welcome-message {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
}

.search-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    border-bottom: 2px solid #deebf7;
}

.search-title {
    font-size: 28px;
    width: 40%;
    padding: 3%;
    height: 2.3em;
    text-align: center;
    margin: 5% 0%;
}

.search-input {
    height: 2em;
    width: 30em;
    margin: 7% 0%;
    font-size: 18px;
}

.reset-search-flex {
    width: 100%;
    display: flex;
    justify-content: center;
}

#reset-search-button {
    font-size: 28px;
    padding: 10px;
}

@media (max-width: 450px), (max-height: 350px) {
    .search-container {
        display: flex;
        flex-direction: column;
    }

    .search-input {
        border: 1px solid grey;
        width: 20em;
    }

    .list-option-headers {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 50%;
        margin: 0%;
    }
    .welcome-message {
        margin: 5px;
    }

    .app-header-item {
        margin: 5px;
        padding: 5px;
    }

    .welcome-message {
        display: none;
    }
}
